html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  color: #1a1b15;
  font-family: "Outfit", sans-serif;
  font-size: 1.6rem;
  height: 100%;
}
@media (max-width: 480px) {
  body {
    min-height: 100%;
  }
}

*, *:before, *:after {
  box-sizing: inherit;
}

.contenedor {
  width: min(95%, 120rem);
  margin: 0 auto;
}

.contenedor-sm {
  width: min(95%, 60rem);
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Outfit", sans-serif;
  font-weight: 900;
  margin: 2rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

p {
  font-size: 2rem;
}

.text-center {
  text-align: center;
}

[class$=__heading] {
  font-family: "Outfit", sans-serif;
  font-weight: 900;
  margin: 2rem 0;
  margin: 6rem 0 3rem 0;
  text-align: center;
}

[class$=__descripcion] {
  text-align: center;
  font-size: 1.8rem;
  font-weight: 300;
  color: #1E293B;
  margin-bottom: 5rem;
}

/*Componenetes*/
.alerta {
  padding: 1rem 1rem 1rem 2rem;
  text-transform: uppercase;
  font-weight: 900;
  margin-bottom: 1rem;
  font-size: 1.2rem;
  max-width: 80rem;
}
.alerta__error {
  background-color: #ffdcdc;
  border-left: 0.5rem solid rgb(169, 0, 0);
  color: rgb(169, 0, 0);
}
.alerta__exito {
  background-color: #ddffdd;
  border-left: 0.5rem solid #02db02;
  color: #02db02;
}

.divAlertas {
  margin: 1rem auto;
}

.boton-azul {
  background-color: #007df4;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  margin: 5rem 0;
  width: auto;
}
@media (max-width: 768px) {
  .boton-azul {
    width: 100%;
  }
}
.boton-azul:hover {
  background-color: #0063c1;
  cursor: pointer;
}
.boton-azul:hover {
  background-color: #002f5b;
}

.boton-gris {
  background-color: #076493;
  padding: 1.5rem 4rem;
  color: #64748B;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  margin: 5rem 0;
  width: auto;
}
@media (max-width: 768px) {
  .boton-gris {
    width: 100%;
  }
}
.boton-gris:hover {
  background-color: #054362;
  cursor: pointer;
}

.boton-eliminar {
  background-color: rgb(169, 0, 0);
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  margin: 5rem 0;
  width: auto;
}
@media (max-width: 768px) {
  .boton-eliminar {
    width: 100%;
  }
}
.boton-eliminar:hover {
  background-color: #760000;
  cursor: pointer;
}

.boton-cerrar {
  background-color: #1a1b15;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  margin: 5rem 0;
  width: auto;
  border: 1px solid #FFFFFF;
}
@media (max-width: 768px) {
  .boton-cerrar {
    width: 100%;
  }
}
.boton-cerrar:hover {
  background-color: black;
  cursor: pointer;
}
.boton-cerrar:hover {
  background-color: #36382b;
}

.swal2-popup {
  font-size: 1.6rem !important;
  font-family: Georgia, serif;
}

/*UI*/
.formulario {
  display: flex;
  flex-direction: column;
}
.formulario__fieldset {
  border: none;
  margin-top: 1rem;
  padding: 0;
}
.formulario__legend {
  color: #64748B;
  font-size: 2.2rem;
  margin-bottom: 2rem;
}
.formulario__campo {
  display: flex;
  align-items: center;
  margin: 1.5rem 0;
  position: relative;
}
.formulario__campo--encima {
  flex-direction: column;
}
.formulario__label {
  flex: 0 0 10rem;
  font-size: 1.8rem;
  color: rgba(26, 27, 21, 0.8);
}
.formulario__label--encima {
  align-self: flex-start;
  flex: 0 0 4rem;
}
.formulario__input {
  padding: 1rem;
  border: 1px solid #b1c7dd;
  border-radius: 0.5rem;
  width: 100%;
  outline: none;
}
.formulario__input--file {
  border: none;
}
.formulario__input--sociales {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.formulario__input--textarea {
  resize: none;
  height: 10rem;
}
.formulario__radio {
  display: flex;
  width: 100%;
  gap: 2rem;
}
.formulario__submit {
  background-color: #007df4;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  margin: 5rem 0;
  width: auto;
  width: auto;
  align-self: flex-start;
}
@media (max-width: 768px) {
  .formulario__submit {
    width: 100%;
  }
}
.formulario__submit:hover {
  background-color: #0063c1;
  cursor: pointer;
}
.formulario__submit--registrar {
  margin: 1rem 0 0 0;
  width: 100%;
}
.formulario__icon {
  position: absolute;
  right: 0;
  margin-right: 0.5rem;
  cursor: pointer;
}
.formulario__contenedor--icono {
  display: flex;
  border-radius: 0.5rem;
  overflow: hidden;
  margin-top: 1rem;
}
.formulario__icono {
  background-color: #1E293B;
  width: 4rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.formulario__icono i {
  color: #FFFFFF;
  font-size: 2rem;
}
.formulario__listado {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  text-align: left;
  width: 100%;
  gap: 1.5rem;
  margin-top: 1.5rem;
}
.formulario__tag {
  background-color: #007df4;
  padding: 1rem;
  border-radius: 0.5rem;
  font-size: 1.4rem;
  font-weight: 700;
  color: #FFFFFF;
  cursor: pointer;
}
.formulario__imagen img {
  max-width: 20rem;
}
.formulario__texto {
  margin-top: 3rem;
}

.acciones, .acciones--centrar {
  margin: 3rem;
  display: flex;
  justify-content: space-between;
}
.acciones__enlaces {
  color: #1a1b15;
  font-size: 1.4rem;
  font-weight: 300;
}
.acciones--centrar {
  justify-content: center;
}

.horas {
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(2, 1fr);
}
@media (max-width: 480px) {
  .horas {
    display: block;
  }
}
.horas__hora {
  color: #007df4;
  border: 1px solid #007df4;
  border-radius: 1rem;
  padding: 1rem;
  text-align: center;
  cursor: pointer;
}
.horas__hora--seleccionado {
  background-color: #007df4;
  color: #FFFFFF;
  font-weight: #1a1b15;
}
.horas__hora--deshabilitado {
  cursor: not-allowed;
  border: 1px solid #64748B;
  color: #64748B;
  opacity: 0.5;
}

.header {
  min-height: 80vh;
  background-image: url(../img/grafico.svg), url(../img/grafico.svg), linear-gradient(to right, #1a1b15, rgba(26, 27, 21, 0.5)), image-set(url(../img/header.webp) 1x, url(../img/header.jpg) 1x);
  background-repeat: no-repeat;
  background-size: 40rem, 40rem, cover, cover;
  background-position: top left, bottom right, center center, center center;
}
@media (max-width: 480px) {
  .header {
    min-height: 100vh;
  }
}
.header__logo {
  font-size: clamp(2rem, -0.8rem + 10vw, 8rem);
  background: linear-gradient(to right, #007df4, #00c8c2);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
@media (max-width: 480px) {
  .header__logo {
    text-align: center;
  }
}
.header__contenedor {
  width: min(95%, 140rem);
  margin: 0 auto;
}
.header__navegacion {
  padding: 5rem 0;
  display: flex;
  justify-content: flex-end;
  gap: 2rem;
}
@media (max-width: 480px) {
  .header__navegacion {
    justify-content: center;
  }
}
.header__contenido {
  padding-top: 3rem;
}
@media (max-width: 480px) {
  .header__contenido {
    padding-top: 10rem;
  }
}
.header__enlace {
  all: unset;
  color: #FFFFFF;
  font-size: 1.4rem;
  text-transform: uppercase;
  font-weight: 700;
  transition: color 0.3s ease;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.header__enlace:hover {
  color: #007df4;
}
.header__texto {
  font-size: 3rem;
  font-weight: 700;
  color: #FFFFFF;
  margin: 2rem 0;
  text-transform: uppercase;
}
@media (max-width: 480px) {
  .header__texto {
    text-align: center;
  }
}
.header__texto--modalidad {
  font-size: 2rem;
}
.header__boton {
  background-color: #00c8c2;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  margin: 5rem 0;
  width: auto;
  display: inline-block;
  text-transform: uppercase;
  margin: 2rem 0;
}
@media (max-width: 768px) {
  .header__boton {
    width: 100%;
  }
}
.header__boton:hover {
  background-color: #009591;
  cursor: pointer;
}
@media (max-width: 480px) {
  .header__boton {
    display: block;
  }
}

.barra {
  background-color: #007df4;
}
.barra__contenido {
  width: min(95%, 140rem);
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 768px) {
  .barra__contenido {
    flex-direction: column;
  }
}
.barra__logo {
  color: #FFFFFF;
  text-align: left;
  margin: 0;
}
@media (max-width: 480px) {
  .barra__logo {
    text-align: center;
  }
}

.navegacion {
  display: flex;
}
@media (max-width: 768px) {
  .navegacion {
    flex-direction: column;
    text-align: center;
  }
}
.navegacion__enlace {
  color: #FFFFFF;
  font-size: 2.2rem;
  padding: 2rem;
  font-weight: 900;
  text-transform: uppercase;
  transition: 0.3s ease;
}
.navegacion__enlace:hover, .navegacion__enlace--fijo {
  background-color: #FFFFFF;
  color: #007df4;
}

.footer {
  background-color: #007df4;
  padding-top: 3rem;
  color: #FFFFFF;
}
.footer__grid {
  width: min(95%, 140rem);
  margin: 0 auto;
  display: grid;
  gap: 3rem;
  grid-template-columns: repeat(2, 1fr);
}
@media (max-width: 768px) {
  .footer__grid {
    display: block;
  }
}
.footer__logo {
  color: #FFFFFF;
  text-align: left;
  margin: 0;
  font-size: clamp(2rem, -0.8rem + 10vw, 4rem);
}
@media (max-width: 480px) {
  .footer__logo {
    text-align: center;
  }
}
.footer__texto {
  line-height: 2;
}
@media (max-width: 480px) {
  .footer__texto {
    text-align: center;
  }
}
.footer__copyright {
  background-color: #0063c1;
  margin: 3rem 0 0 0;
  padding: 2rem 0;
  text-align: center;
  font-size: 1.4rem;
  font-weight: 900;
}
.footer__copyright--regular {
  font-weight: 400;
}

.menu-redes {
  display: flex;
  gap: 2rem;
  margin: 4rem 0;
  justify-content: flex-end;
}
.menu-redes__enlace::before {
  color: #FFFFFF;
  font-size: 2rem;
  display: inline-block;
  font-style: normal;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  font-family: "Font Awesome 6 Brands";
}
.menu-redes__enlace[href*="https://facebook.com"]::before {
  content: "\f39e";
}
.menu-redes__enlace[href*="https://twitter.com"]::before {
  content: "\f099";
}
.menu-redes__enlace[href*="https://youtube.com"]::before {
  content: "\f167";
}
.menu-redes__enlace[href*="https://instagram.com"]::before {
  content: "\f16d";
}
.menu-redes__enlace[href*="https://tiktok.com"]::before {
  content: "\e07b";
}
.menu-redes__enlace[href*="https://github.com"]::before {
  content: "\f09b";
}
.menu-redes__ocultar {
  display: none;
}
@media (max-width: 768px) {
  .menu-redes {
    justify-content: center;
  }
}

/*paginas*/
.auth {
  width: min(95%, 60rem);
  margin: 0 auto;
}
.auth__texto {
  font-size: 2rem;
}

.devwebcamp {
  background-image: url(/build/img/bg-nosotros.svg);
  padding-bottom: 5rem;
}
.devwebcamp__grid {
  width: min(95%, 140rem);
  margin: 0 auto;
  display: grid;
  gap: 5rem;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
}
@media (max-width: 480px) {
  .devwebcamp__grid {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(1, 1fr);
  }
}
.devwebcamp__imagen source, .devwebcamp__imagen img {
  border-radius: 2rem;
  border: 1rem solid #FFFFFF;
}
.devwebcamp__contenido {
  backdrop-filter: blur(5px);
  border-radius: 2rem;
  padding: 1rem;
}
.devwebcamp__texto {
  line-height: 2;
}

.paquetes {
  padding-bottom: 5rem;
}
.paquetes__grid {
  width: min(95%, 140rem);
  margin: 0 auto;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(3, 1fr);
  align-items: flex-start;
}
@media (max-width: 480px) {
  .paquetes__grid {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(1, 1fr);
  }
}

.paquete {
  border: 1px solid #7f8ea3;
  padding: 3rem;
}
.paquete__nombre {
  font-weight: 900;
  font-size: 2.6rem;
  margin: 2rem 0;
}
@media (max-width: 480px) {
  .paquete:nth-child(2) {
    order: -1;
  }
  .paquete:nth-child(3) {
    order: -1;
  }
}
.paquete__lista {
