/*admin*/
.dashboard {
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #F8FAFC;
}
.dashboard__header {
  background-color: #1E293B;
  padding: 2rem;
}
.dashboard__header--grid {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 480px) {
  .dashboard__header--grid {
    justify-content: center;
    flex-direction: column;
  }
}
.dashboard__logo {
  color: #FFFFFF;
  text-align: left;
  margin: 0;
  font-size: clamp(2rem, -0.8rem + 10vw, 3rem);
}
@media (max-width: 480px) {
  .dashboard__logo {
    text-align: center;
  }
}
.dashboard__submit--logout {
  all: unset;
  color: #FFFFFF;
  font-size: 1.4rem;
  text-transform: uppercase;
  font-weight: 700;
  transition: color 0.3s ease;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.dashboard__submit--logout:hover {
  color: #007df4;
}
.dashboard__grid {
  display: flex;
  flex: 1;
  overflow-y: auto;
}
.dashboard__sidebar {
  background-color: #2f405d;
  width: 12rem;
}
.dashboard__menu {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.dashboard__enlace {
  color: #6f8ab6;
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: 0.3s ease-in-out;
  padding: 2rem;
}
.dashboard__enlace:hover, .dashboard__enlace--actual {
  background-color: #40587f;
  color: #FFFFFF;
}
.dashboard__icono {
  font-size: 2rem;
}
.dashboard__menu--texto {
  font-size: 1.1rem;
  text-transform: uppercase;
  font-weight: 700;
  margin-top: 0.5rem;
}
.dashboard__contenido {
  width: 100%;
  padding: 1rem 5rem 3rem 5rem;
  overflow: scroll;
}
@media (max-width: 768px) {
  .dashboard__contenido {
    padding: 1rem 1rem 3rem 1rem;
  }
}
.dashboard__contenido::-webkit-scrollbar {
  width: 1.2rem;
}
.dashboard__contenido::-webkit-scrollbar-thumb {
  background-color: #007df4;
  border-radius: 10px;
  border: 3px solid #ffffff;
}
.dashboard__contenedor--boton {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 4rem;
}
.dashboard__boton {
  background-color: #007df4;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  margin: 5rem 0;
  width: auto;
  display: inline-block;
  text-transform: uppercase;
  margin-top: 0.5rem;
  font-size: 1.4rem;
  width: auto;
}
@media (max-width: 768px) {
  .dashboard__boton {
    width: 100%;
  }
}
.dashboard__boton:hover {
  background-color: #0063c1;
  cursor: pointer;
}
@media (max-width: 768px) {
  .dashboard__boton {
    display: block;
  }
}
.dashboard__contenedor, .dashboard__formulario {
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
  background-color: #FFFFFF;
  padding: 2rem;
  border-radius: 1rem;
}
.dashboard__formulario {
  width: min(95%, 80rem);
  margin: 0 auto;
}

.table {
  width: 100%;
  border-collapse: collapse;
}
.table__thead {
  background-color: #1E293B;
  color: #FFFFFF;
}
.table__tr:nth-of-type(even) {
