  justify-content: flex-end;
  gap: 2rem;
  align-items: center;
}
.table__td--acciones li {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.table__accion {
  all: unset;
  text-transform: uppercase;
  font-size: 1.4rem;
  font-weight: 700;
  cursor: pointer;
}
.table__accion--editar {
  color: #007df4;
}
@media (max-width: 480px) {
  .table__accion--editar {
    display: block;
    text-align: center;
    margin-bottom: 1rem;
